<template>
 <div class="center">
      <div class="center_breadcrumb">
        <crumb :data="pageTitle"></crumb>
    </div>
    <div class="center_content">
         <div class="table-container">
             <el-table
                :data="tableData"  
                stripe
                style="width: 100%">
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="email" label="邮箱"></el-table-column>
                <el-table-column prop="phone" label="手机号"></el-table-column>
                <el-table-column prop="ip" label="ip"></el-table-column>
                <el-table-column
                label="操作"
                width="200">
                <template slot-scope="scope">
                    <div class="operate-button">
                        <SvgIcon icon-class="iconyulan"></SvgIcon>
                        <el-button
                        @click.native.prevent="previewRow(scope.row)"
                        type="text"
                        size="small">
                        查看原始问卷
                        </el-button>
                    </div>                    
                </template>
                </el-table-column>
            </el-table>
            <div class="center_pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.pageSize"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
 </div>
</template>

<script>

export default {
 name: 'OriginalQuestionnaireList',
 components: {},
 data() {
 return {
   pageTitle:['已完成问卷', ' 查看原始问卷'],
   page:{
        formID:this.$route.query.id,
        page:1,
        pageSize:15
     },
   total:0,
   tableData:[],
   api:{
        listOriginalPage:'/form/rest/FormAnserBusiness/listOriginalForPlainPage/v1'
      
    }
 }
 },
 created(){
     this.initData()
 },
 mounted(){},
 methods:{
     initData(){
         this.$post(this.api.listOriginalPage,this.page).then(res =>{
             if(res.code === 200){
                 this.tableData = res.data.rows || []
                 this.total = res.data.total        
             }else{
               this.$notify.error({
                title: 'error',
                duration:1500,
                message: '获取列表失败'
                })
             }
         })
     },
     previewRow(row){
        this.$router.push({
            path:'/evaluationAndFeedback/eResult',
            query:{
                formId:row.formId,
                answerId:row.answerid
                }
            })
     },
     handleSizeChange(val){
        this.page.pageSize = val;
		this.initData()
     },
     handleCurrentChange(obj){
        this.page.page = obj;
        this.initData()
     },
 
 }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss'
</style>



